body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* GAME COMPONENT */
.game {
  margin-top: 2em;
  margin-left: 2em;
  padding: 50px;
  float: left;
  width: 60%;
}

.timer {
  text-align: center;
  margin-bottom: 1em;
}

.thumbnail {
  text-align: center;
}

.titleQuestion {
  padding: 1em;
  text-align: center;
}

.buttons {
  padding: 1em;
  text-align: center;
}

.realButton {
  width: 100px;
  height: 40px;
  font-size: 20px;
  margin-right: 0.5em;
}

.fakeButton {
  width: 100px;
  height: 40px;
  font-size: 20px;
  margin-left: 0.5em;
}

/* HOME COMPONENT */
.welcome {
  text-align: center;
  padding: 1em;
  margin: auto;
}

.logout {
  text-align: right;
  padding: 1em;
}

.eachGame {
  margin-bottom: 2em;
  margin-left: 1em;
}

.historyTitle {
  margin-left: 1em;
  margin-bottom: 1em;
}

.gameText {
  padding: 0em;
}

.title1 {
  margin-left: 6%;
  font-size: 220.42%;
  text-align: center;
  color: rgb(187, 129, 74);
  padding-top: 0.2%;
  
}

.header{
  background-color: rgba(236, 201, 157, 0.849);
}

.history {
  font-size: 190.42%;
  color: rgb(187, 125, 74);
  text-align: center;
 
}

.hBreak {
  opacity: 0.2;
}

.play{
  position: relative;
  margin-left: 40%;
  background-color: rgba(106, 63, 128, 0.527);
  border-radius: 10px;
  width: 20%;
  height: 30%;
  font-size: 20px;
  margin-bottom: 0.3%;
}

.logout{
  position: relative;
  float:right;
  background-color: rgba(106, 63, 128, 0.527);
  border-radius: 10px;
  height: 10%;
  font-size: 14px;
  margin-top: 0.6%;
  margin-right: 0.6%;
}

.gameText{
  padding-left: 0.5%;
  padding-top: 0.1%;
}

.box{
  padding: 0.2%;
  padding-left: 0.5%;
  width: 70%;
  border-radius: 10px;
  height: 12%;
  font-size: 17px;
  background-color: rgba(106, 63, 128, 0.527);
}




